ition: background 0.5s;\n    background-size: 200% 400%;\n    overflow: hidden;\n}\n\n.header__links{\n    display:flex;\n    justify-content: space-evenly;\n    font-size:140%;\n}\n\n.header__links a{\n    font-weight:900;\n    color:white;\n    border-radius: 1rem;\n    border:1px solid brown;\n\n}\n.header__links a:hover{\n    color:lightgrey;\n    font-weight:600;\n    transform:translate(0,1px);\n    box-shadow: 0 1px 0 sandybrown,0 1px 5px black;\n}\n\n.header__links a:active{\n    transform:translate(0,2px);\n    box-shadow:0 0 0 sandybrown, 0 0 1px black;\n}\n\n/* about */\n\n.about{\n    background-color:#D0CD94;\n    border-color:brown;\n\n}\n\n\n.about__info__container{\n    display:flex;\n    justify-content: center;\n    height:100%;\n    width:100%;\n}\n\n.about__info__pic{\n    width:100%;\n    display:flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n.about__info__pic__img{\n    height:300px;\n    width:300px;\n    margin:0 auto;\n    border-radius:50%;\n    overflow:hidden;\n}\n\n.about__info__pic__img img{\n    height:100%;\n}\n\n\n\n.about__info__pic__cap{\n    text-align: center;\n    border: 4px solid brown;\n    /* height: calc(100% - 302px); */\n    border-radius: 10px;\n    padding: 10px;\n    max-width: 30vw;\n    box-sizing: border-box;\n    background-color: #C4D3DE;\n}\n\n\n.about__info__bio{\n    width:100%;\n    justify-content: center;\n    align-items: center;\n}\n\n\n.about__skill-list{\n    background-color: lightgrey;\n    padding: 5em 2em;\n    border-radius: 1em;\n    border:3px solid brown;\n}\n/* Projects */\n.projects{\n    position: relative;\n    border-color:darkgreen;\n    z-index: -1;\n}\n\n\n\n.projects__container{\n    height:100%;\n    width:100%;\n    justify-content: center;\n    gap:0.6em;\n}\n\n.projects__item{\n    width:60%;\n    height:275px;\n    border:3px solid brown;\n    background-color:sandybrown;\n    display:grid;\n    grid-template-areas: 'img info';\n    