  --highlightedText:#a7f6c5;\n    --highlightedLogo:rgb(237, 253, 8);\n    --linkColor:rgb(201, 84, 90);\n    --groundColor:#328D25;\n    --skyColor:skyblue;\n    --coreColor:#FE3C0C;\n    --mantleColor:#F6BE53;\n}\n*{\n    padding:0;\n    margin:0;\n    font-family:'Courier New', Courier, monospace;\n    user-select: none;\n}\nhtml{\n    scroll-behavior: smooth;\n}\nh1,h2,h3,h4,h5,h6,strong{\n    color:var(--mainText);\n    /* background: linear-gradient(to-right, #62cff4, #2c67f2,  #23d5ab,#23a6d5, #e73c7e, #ee7752, #DD8267, #CB8D7B, #A8A3A3,#85B9CC,#74C4E0) */\n    /* font-family: 'Roboto', sans-serif; */\n}\n\np,li,label,input,textarea{\n    color:var(--secondaryText);\n    padding:0.3rem;\n}\n\na{\n    text-decoration: none;\n    color:inherit;\n}\n\nbody{\n    position: relative;\n}\n    \n#main-content {\n    z-index: 0;\n    position: absolute;\n    display: grid;\n    grid-template-areas: 'nav'\n    'header'\n    'projects'\n    'about'\n    'footer';\n    position: relative;\n    grid-template-columns: 1fr;\n}\nsection{\n    background-color: var(--primaryBGColor);\n    box-sizing: border-box;\n}\n\nsection h1{\n    font-size:130%;\n    padding:0;\n    margin:0;\n}\n\nsection .title{\n    font-size:250%;\n}\n\nsection .content{\n    font-size:130%;\n}\n.death-screen{\n    display:none;\n    position:fixed;\n    width:100vw;\n    height:100vh;\n    z-index: 1000;\n    background-color: white;\n    font-size:200%;\n}\n\n.rocket {\n    transform: rotate(-45deg) scale(10);\n    position:absolute;\n    bottom: 10vh;\n    left: 50vw;\n    color:orange;\n}\n\n/* spacing */\n\n#header{\n    margin-top: 9rem;\n    padding-top: 10rem;\n    padding-bottom: 15rem;\n    background: linear-gradient(-45deg,#74C4E0, #85B9CC,#A8A3A3, #ee7752  , #e73c7e,#23a6d5 ,#23d5ab ,#2c67f2,#62cff4);\n    transition: background 0.5s;\n    background-size: 200% 400%;\n    overflow: hidden;\n}\n\n.header__links{\n    display:flex;\n    justify-content: space-evenly;\n    font-size:140%;\n}\n\n.header__links a{\n    font-weight:900;\n    color:white;\n    border-radius: 1rem;\n    border:1px solid brown;\n\n}\n.header__links a:hover{\n    color:lightgrey;\n    font-weight:600;\n    transform:translate(0,1px);\n    box-shadow: 0 1px 0 sandybrown,0 1px 5px black;\n}\n\n.header__links a:active{\n    transform:translate(0,2px);\n    box-shadow:0 0 0 sandybrown, 0 0 1px black;\n}\n\n/* about */\n\n.about{\n    background-color:#D0CD94;\n    border-color:brown;\n\n}\n\n\n.about__info__container{\n    display:flex;\n    justify-content: center;\n    height:100%;\n    width:100%;\n}\n\n.about__info__pic{\n    width:100%;\n    display:flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n.about__info__pic__img{\n    height:300px;\n    width:300px;\n    margin:0 auto;\n    border-radius:50%;\n    overflow:hidden;\n}\n\n.about__info__pic__img img{\n    height:100%;\n}\n\n\n\n.about__info__pic__cap{\n    text-align: center;\n    border: 4px solid brown;\n    /* height: calc(100% - 302px); */\n    border-radius: 10px;\n    padding: 10px;\n    max-width: 30vw;\n    box-sizing: border-box;\n    background-color: #C4D3DE;\n}\n\n\n.about__info__bio{\n    width:100%;\n    justify-content: center;\n    align-items: center;\n}\n\n\n.about__skill-list{\n    background-color: lightgrey;\n    padding: 5em 2em;\n    border-radius: 1em;\n    border:3px solid brown;\n}\n/* Projects */\n.projects{\n    position: relative;\n    border-color:darkgreen;\n    z-index: -1;\n}\n\n\n\n.projects__container{\n    height:100%;\n    width:100%;\n    justify-content: center;\n    gap:0.6em;\n}\n\n.projects__item{\n    width:60%;\n    height:275px;\n    border:3px solid brown;\n    background-color:sandybrown;\n    display:grid;\n    grid-template-areas: 'img info';\n    grid-template-columns: 300px 1fr;\n}\n\n.project__title{\n    color: saddlebrown;\n}\n\n.projects__item__img{\n    grid-area:img;\n}\n\n.projects__item__img img{\n    width:100%;\n}\n\n.projects__item__img i{\n    font-size: 100px;\n}\n\n.projects__item__info{\n    grid-area:info;\n    align-items: flex-start;\n    justify-content: space-evenly;\n    padding:0.5em 0.3em;\n}\n\n.projects__item:hover{\n    box-shadow: 0px 0px 10px whitesmoke;\n    transform:translate(0,-2px);\n    transition:transform 0.2s;\n    cursor:pointer;\n}\n\n\n\n.main-container{\n    height:100%;\n    width: 100%;\n    padding: 3rem;\n    padding-top: 10rem;\n    padding-bottom: 15rem;\n    box-sizing: border-box;\n    margin:0 auto;\n    overflow:hidden;\n    position:relative;\n}\n\n.main-container h1{\n    text-align: center;\n    margin-bottom: 8vw;\n}\n\n\n\n\n.border{\n    /* border-style: solid; */\n    border-width: 0.2em;\n    border-bottom: none;\n    border-left: none;\n    border-right: none;\n}\n\n.border-sky{\n    border-top-left-radius: 67% 6%;\n    border-top-right-radius: 67% 6%;\n}\n\n.border-surface{\n    border-top-left-radius: 64% 14%;\n    border-top-right-radius: 64% 14%;\n}\n.border-mantle{\n    border-top-left-radius: 60% 14%;\n    border-top-right-radius: 60% 14%;\n}\n.border-core{\n    border-top-left-radius: 60% 100%;\n    border-top-right-radius: 60% 100%;\n}\n\n/* media  */\n\n\n@media(min-width:2160px){\n    \n    .border-sky{\n        border-top-left-radius: 67% 6%;\n        border-top-right-radius: 67% 6%;\n    }\n    \n    .border-surface{\n        border-top-left-radius: 64% 25%;\n        border-top-right-radius: 64% 25%;\n    }\n    .border-mantle{\n        border-top-left-radius: 60% 25%;\n        border-top-right-radius: 60% 25%;\n    }\n    .border-core{\n        border-top-left-radius: 40% 100%;\n        border-top-right-radius: 40% 100%;\n    }\n\n    section .title{\n        font-size:250%;\n    }\n\n    section .content{\n        font-size:150%;\n    }\n    .header__links a{\n        font-size:130%;\n        padding:0.4em 0.3em;\n    }\n}\n@media(max-width:2160px){\n    h1,h2,h3,h4,h5,h6{\n        font-size:130%;\n    }\n\n 